// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
// 引入路由插件
import VueRouter from 'vue-router';
// 引入ajax插件
import VueResource from 'vue-resource';
import App from './App';
import goods from './components/goods/goods';
import seller from './components/seller/seller';
import ratings from './components/ratings/ratings';
// 加载公共样式
import './common/css/index.css';

// 安装VueRouter插件
Vue.use(VueRouter);
// 安装VueResource插件
Vue.use(VueResource);

// 定义一个routes数组
let routes = [{
  path: '/',
  // name: 'index',
  component: App,
  children: [{
    path: '/goods',
    component: goods
  }, {
    path: '/ratings',
    component: ratings
  }, {
    path: '/seller',
    component: seller
  }]
}];

// var routes = [{
//   // Router构造函数配置中path下是不能加点的，这点和
//   // 因为这里是路由
//   path: '/goods',
//   component: goods
// }, {
//   path: '/ratings',
//   component: ratings
// }, {
//   path: '/seller',
//   component: seller
// }];

// 创建一个路由实例并传入配置
var router = new VueRouter({
  routes,
  // 配置全局切换时的类名
  linkActiveClass: 'active'
});

/* eslint-disable no-new */
// new Vue({
//   el: '#app',
//   template: '<App/>',
//   components: { App },
//   // 把路由挂载到这个vue上
//   router
// });
let app = new Vue({
  router
  // template: '<App/>'
  // components: { App }
}).$mount('#app');

  // router.push('/goods');
export default app;
// 在刷新页面时导航到指定页面
router.push('/goods');



