<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count" v-on:click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" v-on:click="showDetail">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">
        {{seller.bulletin}}
      </span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div v-if="detailShow" class="detail">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="36" :score="seller.score"></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item, index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">
                  {{seller.supports[index].description}}
                </span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="hideDetail">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>

  </div>
</template>

<script type="text/esmascript-6">
import star from '../stars/star.vue';
export default {
  props:{
    seller:{
      type:Object
    }
  },
  data(){
    return {
      detailShow: false
    };
  },
  created() {
    this.classMap=['decrease','discount','special','invoice','guarantee'];
  },
  components:{
    star
  },
  methods:{
    showDetail() {
      this.detailShow = true;
    },
    hideDetail() {
      this.detailShow = false;
    }
  }
};
</script>

<style type="text/ecmascript-6">
.header{
  position: relative;
  overflow: hidden;
  color: #fff;
  background-color: rgba(7,17,27,.5);
}
.content-wrapper{
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.content-wrapper .avatar{
  display: inline-block;
  vertical-align: top;
}
.content-wrapper .avatar img{
  border-radius: 2px;
}
.content-wrapper .content{
  display: inline-block;
  margin-left: 16px;
}
.content-wrapper .content .title{
  margin: 2px 0 8px 0;
}
.content-wrapper .content .brand{
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-size: 30px 18px;
}

.content-wrapper .content .name{
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}
.content-wrapper .description{
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 12px;
}

.content-wrapper .support .icon{
  width: 12px;
  height: 12px;
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  background-size: 12px 12px;
}
.content-wrapper .support .text{
  line-height: 12px;
  font-size: 10px;
}
.support-count{
  position: absolute;
  right: 12px;
  bottom: 14px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  background-color: rgba(0,0,0,.2);
  text-align: center;
  border-radius: 14px;
}
.support-count .count{
  vertical-align: top;
  font-size: 10px;
}
.support-count .icon-keyboard_arrow_right{
  margin-left: 2px;
  line-height: 24px;
  font-size: 10px;
}
.bulletin-wrapper{
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  background-color: rgba(7,17,27,.2);
}
.bulletin-wrapper .bulletin-title{
  display: inline-block;
  vertical-align: top;
  margin-top: 8px;
  width: 22px;
  height: 12px;
  background-size: 22px 12px;
}
.bulletin-wrapper .bulletin-text{
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.bulletin-wrapper .icon-keyboard_arrow_right{
  position: absolute;
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.background{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  filter: blur(10px);

}
.detail{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(7,17,27,.8);
  -webkit-backdrop-filter:blur(10px);
  /*opacity: 1;*/
}
.fade-enter-active,.fade-leave-active{
  transition: opacity 1s;
}
.fade-enter,.fade-leave-active{
  opacity: 0;
}
.detail-wrapper{
  min-height: 100%;
  width: 100%;
}
.detail-wrapper .detail-main{
  margin-top: 64px;
  padding-bottom: 64px;
}
.detail-main .name{
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
}
.detail-main .star-wrapper{
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}
.detail-main .title{
  width: 80%;
  margin: 28px auto 24px auto;
  display: flex;
}
.detail-main .title .line{
  flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,.2);
}
.detail-main .title .text{
  font-size: 14px;
  padding: 0 12px 0 12px;
  font-weight: 700;
}
.detail-main .supports{
  width: 80%;
  margin: 0 auto;
}
.detail-main .supports .support-item{
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.support-item .icon{
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.support-item .text{
  font-size: 12px;
  line-height: 16px;
}
.detail-main .bulletin{
  width: 80%;
  margin: 0 auto;
}
.bulletin .content{
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.detail-close{
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
}
.detail-close .icon-close{

}
/*必需加webkit*/
@media(-webkit-min-device-pixel-ratio:2){
  .content-wrapper .content .brand{
    background-image: url(brand@2x.png);
  }
  .support .decrease{
    background-image: url(decrease_1@2x.png);
  }
  .support .discount{
    background-image: url(discount_1@2x.png);
  }
  .support .guarantee{
    background-image: url(guarantee_1@2x.png);
  }
  .support .invoice{
    background-image: url(invoice_1@2x.png);
  }
  .support .special{
    background-image: url(special_1@2x.png);
  }
  .support-item .decrease{
    background-image: url(decrease_2@2x.png);
  }
  .support-item .discount{
    background-image: url(discount_2@2x.png);
  }
  .support-item .guarantee{
    background-image: url(guarantee_2@2x.png);
  }
  .support-item .invoice{
    background-image: url(invoice_2@2x.png);
  }
  .support-item .special{
    background-image: url(special_2@2x.png);
  }
  .bulletin-wrapper .bulletin-title{
    background-image: url(bulletin@2x.png);
