<template>
  <div>
    <div class="shopcart">
      <div class="content" @click="toggleList">
        <div class="content-left">
          <div class="logo-wrapper">
            <span class="logo" :class="{'high-light':totalCount>0}">
              <i class="icon-shopping_cart"></i>
            </span>
            <div class="num" v-show="totalCount>0">
              {{totalCount}}
            </div>
          </div>
          <div class="price" :class="{'high-light':totalPrice>0}">
            ￥{{totalPrice}}
          </div>
          <div class="desc">
            另需配送费￥{{deliveryPrice}}元
          </div>
        </div>
        <div class="content-right" @click.stop.prevent="pay">
          <div class="pay" :class="payClass">
            {{payDesc}}
          </div>
        </div>
      </div>
      <div class="ball-container">
        <div v-for="ball in balls">
          <transition name="drop" v-on:enter="enter" v-on:before-enter="beforeEnter" v-on:after-enter="afterEnter">
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price*food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade-mask">
      <div class="list-mask" v-if="listShow" @click="hideList">
      </div>
    </transition>
  </div>
</template>

<script type="text/esmascript-6">
import cartcontrol from '../cartcontrol/cartcontrol';
import BScroll from 'better-scroll';
export default {
  components:{
    cartcontrol
  },
  methods:{
    pay(){
      if (this.totalPrice<this.minPrice) {
        return;
      }
      alert(`需要支付${this.totalPrice}`);
    },
    hideList(){
      this.fold=true;
    },
    empty(){
      this.selectFoods.forEach((food)=>{
        food.count=0;
      });
    },
    toggleList(){
      if (!this.totalCount) {
        return;
      }
      this.fold=!this.fold;
    },
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          // 触发动画
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    beforeEnter(el) {
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 22);
          el.style.display = '';
          el.style.webkitTransform = `translate3d(0,${y}px,0)`;
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter(el) {
      let rf = el.offestHeight;
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0,0,0)';
        el.style.transform = 'translate3d(0,0,0)';
        let inner = el.getElementsByClassName('inner-hook')[0];
        inner.style.webkitTransform = 'translate3d(0,0,0)';
        inner.style.transform = 'translate3d(0,0,0)';
      });
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    }
  },
  data(){
    return {
      balls:[{
        show:false
      },{
        show:false
      },{
        show:false
      },{
        show:false
      },{
        show:false
      }],
      dropBalls:[],
      fold:true
    };
  },
  props:{
    // 购物车的联动数组
    selectFoods: {
      type: Array,
      default () {
        return [{
          price: 10,
          count: 5
        }];
      }
    },
    deliveryPrice:{
      type:Number,
      default:0
    },
    minPrice:{
      type:Number,
      default:0
    }
  },
  computed:{
    // 计算属性里的方法，会自动调用，方法用等同于属性名
    totalPrice(){
      let total=0;
      this.selectFoods.forEach((food) => {
        total += food.price * food.count;
      });
      return total;
    },
    totalCount(){
      let count=0;
      this.selectFoods.forEach((food)=>{
        count+=food.count;
      });
      return count;
    },
    payDesc(){
      if (this.totalPrice===0) {
        return `￥${this.minPrice}元起送`;
      }else if (this.totalPrice<this.minPrice) {
        let diff=this.minPrice-this.totalPrice;
        return `还差${diff}元起送`;
      }else{
        return '去结算';
      }
    },
    payClass(){
      if (this.totalPrice<this.minPrice) {
        return 'not-enough';
      }else{
        return 'enough';
      }
    },
    listShow(){
      if (!this.totalCount) {
        this.fold=true;
        return false;
      }
      let show= !this.fold;
      if (show) {
        this.$nextTick(()=>{
          if (!this.scroll) {
            this.scroll=new BScroll(this.$refs.listContent,{
              click:true
            });
          }else{
            this.scroll.refresh();
          }
        });
        return show;
      }
    }
  }
};
</script>

<style>
.shopcart{
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 99;
  width: 100%;
  height: 48px;
}
.shopcart .content{
  display: flex;
  background-color: #141d27;
}
.content .content-left{
  flex: 1;
  font-size: 0;
}
.content .content-right{
  flex: 0 0 105px;
  width: 105px;
}
.content-left .logo-wrapper{
  display: inline-block;
  position: relative;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  background-color: #141d27;
}
.logo-wrapper .logo{
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.logo-wrapper .high-light{
  background: rgb(0,160,220);
}
.logo-wrapper .num{
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  text-align: center;
  line-height: 16px;
  border-radius: 16px;
  font-weight: 700;
  background: rgb(240,20,20);
  color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);
  font-size: 10px;
}
.logo .icon-shopping_cart{
  font-size: 24px;
  color: #80858a;
  line-height: 44px;
}
.high-light .icon-shopping_cart{
  color: #fff;
}
.content-left .price{
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  padding-right:12px;
  line-height: 24px;
  font-weight: 700;
  font-size: 16px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,.1);
  color:rgba(255,255,255,.4);
}
.content-left .high-light{
  color: #fff;
}
.content-left .desc{
  display: inline-block;
  vertical-align: top;
  margin:12px 0 0 12px;
  font-size: 10px;
  line-height: 24px;
  color:rgba(255,255,255,.4);
}
.content-right .pay{
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  background: #2b333b;
  color: rgba(255,255,255,.4);
}
.content-right .not-enough{
  background: #2b333b;
}
.content-right .enough{
  background: #00b43c;
  color: #fff;
}
.ball-container .ball{
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41);
}
.ball .inner{
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: rgb(0,160,220);
  transition: all .4s linear;
}
.shopcart-list{
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  transition: transform .4s;
  transform: translate3d(0,-100%,0);
}

.shopcart .fold-enter-active,.shopcart .fold-leave-active{
  transform: translate3d(0,-100%,0);
}
.shopcart .fold-enter,.shopcart .fold-leave-active{
  transform: translate3d(0,0,0);
}
.shopcart-list .list-header{
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.list-header .title{
  float: left;
  font-size: 14px;
  color: rgb(7,17,27);
}
.list-header .empty{
  float: right;
  font-size: 12px;
  color: rgb(0,160,220);
}
.list-content{
  padding: 0 18px;
  overflow: hidden;
  max-height: 217px;
  background: #fff;
}
.shopcart .food{
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.shopcart .food .name{
  line-height: 24px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.shopcart .food .price{
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(240,20,20);
}
.shopcart .food .cartcontrol-wrapper{
  right: 0;
  bottom: 6px;
}
.list-mask{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7,17,27,.6);
  backdrop-filter:blur(10px);
  transition: all .4s;
}
.fade-mask-enter,.fade-mask-leave-active{
  opacity: 0;
}
</style>



