<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span class="block positive" @click="select(2,$event)" :class="{'active':selectType===2}">{{desc.all}} <span class="count">{{ratings.length}}</span> </span>
      <span class="block positive" @click="select(0,$event)" :class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
      <span class="block negative" @click="select(1,$event)" :class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
    </div>
    <div class="switch" :class="{'on':onlyContent}" @click="toggleContent">
      <span class="icon-check_circle"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
// 定义常量表示评价类型
const POSITIVE = 0;
const NEGATIVE = 1;
const All = 2;
export default{
  methods: {
    select(type, event) {
      if (!event._constructed) {
        return;
      }
      // 通过改变子组件本身的data来实现
      // 这里不能直接修改父组件传过来的props
      // 通知父组件
      this.$parent.$emit('ratingtype.select', type);
    },
    toggleContent(event) {
      if (!event._constructed) {
        return;
      }
      // debugger;
      // 通知父组件
      this.$parent.$emit('content.toggle', !this.onlyContent);
    }
  },
  props: {
    ratings: {
      type: Array,
      default () {
        return [];
      }
    },
    selectType: {
      type: Number,
      default: All
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default () {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        };
      }
    }
  },
  data() {
    return {

    };
  },
  computed: {
    positives() {
      return this.ratings.filter(rating => {
        return rating.rateType === POSITIVE;
      });
    },
    negatives() {
      return this.ratings.filter(rating => {
        return rating.rateType === NEGATIVE;
      });
    }
  }
};
</script>
<style>
.ratingselect{

}
.rating-type{
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,.1);
  font-size: 0;
}
.rating-type .block{
  display: inline-block;
  padding: 8px 12px;
  border-radius: 1px;
  margin-right: 8px;
  font-size: 12px;
  color: rgb(77,85,93);
}
.block.active{
  color: #fff;
}
.block .count{
  font-size: 8px;
  vertical-align: bottom;
}
.block.positive{
  background: rgba(0,160,220,.2);
}
.block.positive.active{
  background: rgb(0,160,220);
}
.block.negative{
  background: rgba(77,85,93,.2);
}
.block.negative.active{
  background: rgb(77,85,93);
}
.switch{
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 2px solid rgba(7,17,27,.1);
  color: rgb(147,153,159);
  font-size: 0;
}
.switch.on .icon-check_circle{
  color: #00c850;
}
.switch .icon-check_circle{
  margin-right: 4px;
  font-size: 24px;
}
.switch .text{
  font-size: 12px;
  vertical-align: top;
}
</style>



