<template>
  <transition name="move">
    <div class="food" v-show="showFlag" ref="food">
      <div class="food-content">
        <div class="img-header">
          <img :src="food.image">
          <div class="back" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="food-detail">
            <span class="sell-count">月售{{food.sellCount}}</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontrol :food="food"></cartcontrol>
          </div>
          <transition name="fade-buy">
            <div class="buy" @click.stop="addFirst($event)" v-show="!food.count||food.count===0">
              加入购物车
            </div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h1 class="title">商品介绍</h1>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img height="12" width="12" class="avatar" :src="rating.avatar"/>
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <div class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                </div>
              </li>
            </ul>
            <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/esmascript-6">
import BScroll from 'better-scroll';
import cartcontrol from '../cartcontrol/cartcontrol.vue';
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import {formatDate} from '../../common/js/date.js';
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
  components:{
    cartcontrol,
    split,
    ratingselect
  },
  props:{
    food:{
      type:Object
    }
  },
  data(){
    return {
      // 是否显示的标志位
      showFlag:false,
      // 定义传给ratingselect组件的显示类型
      selectType:ALL,
      // 定义传给ratingselect组件的显示内容
      onlyContent:false,
      // 定义传给ratingselect组件的显示按钮文本
      desc:{
        all:'全部',
        positive:'推荐',
        negative:'吐槽',
      }
    }
  },
  created() {
    this.$on('ratingtype.select', (type) => {
      this.selectType = type;
      this.$nextTick(()=>{
        this.scroll.refresh();
      })
    });
    this.$on('content.toggle', (contentFlag) => {
      this.onlyContent = contentFlag;
      this.$nextTick(()=>{
        this.scroll.refresh();
      })
    })
  },
  filters:{
    formatDate(time){
      let date=new Date(time);
      return formatDate(date,'yyyy-MM-dd hh:mm');
    }
  },
  methods:{
    needShow(type, text) {
      // debugger;
      // 如果只显示内容，但是评论又没有内容，则直接返回
      if (this.onlyContent && !text) {
        return false;
      }
      //
      if (this.selectType === ALL) {
        return true;
      } else {
        return type === this.selectType;
      }
    },
    show(){
      // 否显示的标志位
      this.showFlag = true;
      // 调用ratingselect组件时默认
      // 显示所有
      this.selectType = ALL;
      this.onlyContent = true;
      // ！！！不用$nextTick不会出现效果
      // 让商口单页实现滚动效果
      this.$nextTick(()=>{
        if (!this.scroll) {
          this.scroll=new BScroll(this.$refs.food,{
            click:true
          });
        }else{
          this.scroll.refresh();
        }
      });
    },
    hide(){
      this.showFlag=false;
    },
    addFirst(event){
      if (!event._constructed) {
        return;
      }
      this.$set(this.food,'count',1);
      // 触发小球运动
      this.$parent.$emit('cart.add',event.target);
    }
  }
};
</script>

<style>
.food{
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  background: #fff;
  width: 100%;
  transition: all .2s;
}
.move-enter-active,.move-leave-active{
  transform: translate3d(0,0,0);
}
.move-enter,.move-leave-active{
  transform: translate3d(100%,0,0);
}
.foot-content{
  /*position: relative;*/
}
.food-content .img-header{
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food-content .img-header img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-content .img-header .back{
  position: absolute;
  top: 0;
  left: 0;
}
.food-content .img-header .back .icon-arrow_lift{
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food-content .content{
  position: relative;
  padding: 18px;
}
.food-content .content .title{
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(7,17,27);
}
.food-content .content .food-detail{
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
  color: rgb(147,153,159);
}
.food-detail .sell-count{
  font-size: 10px;
}
.food-detail .rating{
  margin-left: 12px;
  font-size: 10px;
}
.food-content .content .price{
  font-weight: 700;
  line-height: 24px;
}
.content .price .now{
  margin-right: 8px;
  font-size: 14px;
  color: rgb(240,20,20);
}
.content .price .old{
  font-size: 10px;
  color: rgb(147,153,159);
  text-decoration: line-through;
}
.food-content .cartcontrol-wrapper{
  bottom: 12px;
  right: 12px;
}
.food-content .buy{
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: rgb(0,160,220);
  transition: opacity 0.4s;
}
.fade-buy-enter-active,.fade-buy-leave{
  opacity: 1;
}
.fade-buy-enter,.fade-buy-leave-active{
  opacity: 0;
}
.food .info{
  padding: 18px;
}
.food .info .title{
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.food .info .text{
  line-height: 24px;
  font-size: 12px;
  color: rgb(77,85,93);
  padding: 0 8px;
}
.rating{
  padding-top: 18px;
}
.rating .title{
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.rating-wrapper{
  padding: 0 18px;
}
.rating-wrapper .rating-item{
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.rating-item .user{
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.user .name{
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.user .avatar{
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
}
.rating-item .time{
  line-height: 12px;
  margin-bottom: 6px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.rating-item .text{
  line-height: 16px;
  font-size: 12px;
  color: rgb(7,17,27);
}
.rating-item .icon-thumb_down{
  margin-right: 4px;
  font-size: 12px;
  color:rgb(147,153,159);
  line-height: 16px;
}
.rating-item .icon-thumb_up{
  margin-right: 4px;
  font-size: 12px;
  color: rgb(0,160,220);
  line-height: 16px;
}
.no-rating{
  padding: 16px 0;
  font-size: 12px;
  color: rgb(147,153,159);
}
</style>



