<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectMenu(index,$event)">
            <span class="text">
              <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <li v-for="item in goods" class="food-list food-list-hook">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li v-for="food in item.foods" @click="selectFood(food,$event)" class="food-item">
                <div class="icon">
                  <img :src="food.icon" width="57" height="57">
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">月售{{food.sellCount}}</span>
                    <span class="rating">好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span>
                    <span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    </div>
    <food :food="selectedFood" ref="food"></food>
  </div>
</template>

<script type="text/esmascript-6">
import BScroll from 'better-scroll';
import shopcart from '../shopcart/shopcart.vue';
import cartcontrol from '../cartcontrol/cartcontrol.vue';
import food from '../food/food.vue';
const ERR_OK = 0;
export default {
  components:{
    shopcart,
    cartcontrol,
    food
  },
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood:{}
    }
  },
  created() {
    this.$http.get('/api/goods').then((response) => {
      response = response.body;
      if (response.erron === ERR_OK) {
        this.goods = response.data;
        this.$nextTick(() => {
          this._initScroll();
          this._calculateHeight();
        });
      }
    });
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    this.$on('cart.add',(target)=>{
      this._drop(target);
    });
  },
  methods: {
    _drop(el){
      // 为体验，异步执行下一个动画
      this.$nextTick(()=>{
        this.$refs.shopcart.drop(el);
      });
    },
    selectFood(food,event){
      if(!event._constructed){
        return;
      }
      this.selectedFood=food;
      this.$refs.food.show();
    },
    _initScroll() {
      this.menuSroll = new BScroll(this.$refs.menuWrapper,{
        click: true
      });
      this.foodsScroll=new BScroll(this.$refs.foodsWrapper,{
        probeType:3,
        click: true
      });
      this.foodsScroll.on('scroll',(pos)=>{
        this.scrollY=Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (var i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(index,event){
      // 判断是不是better-scroll派发的事件对象
      // 如果不是则不执行主逻辑
      if(!event._constructed){
        return;
      }
      let foodList=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let el=foodList[index];
      this.foodsScroll.scrollToElement(el,300);
    }
  },
  computed:{
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2||(this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  }
};
</script>

<style>
.goods{
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 46px;
  display: flex;
  overflow: hidden;
}
.menu-wrapper{
  flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.menu-wrapper .menu-item{
  display: table;
  height: 54px;
  width: 56px;
  padding: 0 12px;
  line-height: 14px;
}
.menu-item .icon{
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}

.menu-item .text{
  display: table-cell;
  position: relative;
  width: 56px;
  font-size: 12px;
  vertical-align: middle;
}
.menu-wrapper .current{
  position: relative;
  margin-top: -1px;
  z-index: 10;
  background: #fff;
}
.menu-wrapper .current .text{
  font-weight: 700;
  border:none;
}
.menu-wrapper .current .text:after{
  display: none;
}
.menu-item .text:after{
  display: block;
  left: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  content: "";
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.foods-wrapper{
  flex: 1;
}
.foods-wrapper .title{
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  background: #f3f5f7;
  color: rgb(147,153,159);
}
.foods-wrapper .food-item{
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food-item .icon{
  flex: 0 0 57px;
  margin-right: 10px;
}
.foods-wrapper .food-item img{
  vertical-align: top;
}
.foods-wrapper .food-item:last-child{
  margin-bottom: 0;
  border-bottom: none;
}
.food-item .content{
  flex: 1;
  position: relative;
}
.food-item .content .name{
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.food-item .content .desc{
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.food-item .content .extra{
  font-size: 0;
  color: rgb(147,153,159);
}
.food-item .content .extra span{
  font-size: 10px;
}
.food-item .content .extra .count{
  font-size: 10px;
  margin-right: 12px;
}
.food-item .content .price{
  font-size: 0;
  line-height: 24px;
}
.food-item .content .price .now{
  font-weight: 700;
  margin-right: 8px;
  font-size: 14px;
  color: rgb(240,20,20);
}
.food-item .content .price .old{
  text-decoration: line-through;
  font-size: 10px;
  color: rgb(147,153,159);
}
.cartcontrol-wrapper{
  position: absolute;
  right: 0;
  bottom: -12px;
}
@media(-webkit-min-device-pixel-ratio:2){
  .menu-item .decrease{
    background-image: url(decrease_3@2x.png);
  }
  .menu-item .discount{
    background-image: url(discount_3@2x.png);
  }
  .menu-item .guarantee{
    background-image: url(guarantee_3@2x.png);
  }
  .menu-item .invoice{
    background-image: url(invoice_3@2x.png);
  }
  .menu-item .special{
    background-image: url(special_3@2x.png);
  }
}
@media(-webkit-min-device-pixel-ratio:3){
  .menu-item .decrease{
    background-image: url(decrease_3@3x.png);
  }
  .menu-item .discount{
    background-image: url(discount_3@3x.png);
  }
  .menu-item .guarantee{
    background-image: url(guarantee_3@3x.png);
  }
  .menu-item .invoice{
    background-image: url(invoice_3@3x.png);
  }
  .menu-item .special{
    background-image: url(special_3@3x.png);
  }
}
</style>



